import pyreact



############################################
###  A digital clock

class Clock(pyreact.Component):
    def __init__(self, props):
        super().__init__(props)
    

    def render(self):
        # example of using JSX
        # using a string here is temporary - we need to get 
        # Transcrypt to do it at compile time (not at runtime)
        return [
            self.element(Hour, { 'hour': self.state.hour }),
            ' : ',
            self.element(Minute, { 'minute': self.state.minute }),
        ]



############################################
###   Main method that bootstraps React

def main():
    # example of using React.createElement directly
    ReactDOM.render(
        React.createElement(App, {}),
        document.getElementById('approot')
    );

S(main)


